import { useState } from "react";
function FindPassword() {
  const [inputs, setInputs] = useState({
    email: "",
    id: "",
  });
  const { email, id } = inputs;
  const onChange = (e) => {
    const { value } = e.target;
    setInputs({
      ...inputs,
      [email]: value,
      [id]: value,
    });
  };

  return (
    <div className="find_password_parent_box">
      <div className="find_password_box">
        <div className="find_password_box_tw">
          <div className="find_password_title"> </div>
          <div className="find_password_input">
            <form>
              <div className="find_password_email">
                <input type="email" placeholder="" onChange={onChange} />
              </div>
              <div className="find_password_id">
                <input type="text" placeholder="" onChange={onChange} />
              </div>
              <div className="find_password_btn">
                <input type="submit" value=" " />
              </div>
            </form>
          </div>
        </div>
      </div>
      {/* <div className="already_account">  ?</div> */}
    </div>
  );
}
export default FindPassword;
