import { useState } from "react";
import axios from "axios";
function FindAccount() {
  const [message, setMessage] = useState(""); //  
  const [email, setEmail] = useState(""); //

  //email State  
  const onChangeEmail = (e) => {
    setEmail(e.currentTarget.value);
  };

  //  
  const findAccount = () => {
    let url = "/regist/find/" + email;
    //api 
    axios.get(url, {}).then((res) => {
      if (res.data === "SUCCESS") {
        console.log(" .");
        setMessage(" .");
      } else {
        console.log("   .");
        setMessage("   .");
      }
    });
  };

  return (
    <div className="find_account_parent_box">
      <div className="find_account_box">
        <div className="find_account_box_tw">
          <div className="find_account_title"> </div>
          <div className="find_account_input">
            <form>
              <div className="find_account_email">
                <input type="text" placeholder="" onChange={onChangeEmail} value={email} />
              </div>
              <div className="message_find_account">{message}</div>
              <div className="find_account_btn">
                <input type="button" value=" " onClick={findAccount} />
              </div>
            </form>
          </div>
        </div>
      </div>
      {/* <div className="already_account">  ?</div> */}
    </div>
  );
}
export default FindAccount;
