import "./Login.css";
import { useNavigate } from "react-router-dom";
import { useState } from "react";
//import { getUserSession, getUserInfo } from "../utils/open-sf-utils";

import axios from "axios";
function Login() {
  const [userAccount, setUserAccount] = useState(""); //
  const [userPassword, setUserPassword] = useState(""); //
  const [loginFailMessage, setLoginFailMessage] = useState(false); //  

  //  
  const navigate = useNavigate();

  //  
  const onChangeAccount = (e) => {
    setUserAccount(e.currentTarget.value);
    setLoginFailMessage(false);
  };

  //  
  const onChangePassword = (e) => {
    setUserPassword(e.currentTarget.value);
    setLoginFailMessage(false);
  };
  //Enter Key press  
  const onKeyPress = (e) => {
    if (e.key === "Enter") {
      onLogin();
    }
  };
  // //
  // const findAccount = () => {
  //   navigate("/find/account");
  // };
  // //
  // const signUp = () => {
  //   navigate("/signup");
  // };

  //  
  const onLogin = () => {
    // url
    let url = "/login";
    //api 
    axios
      .post(url, {
        user_account: userAccount,
        user_password: userPassword,
      })
      .then((resp) => {
        console.log("onLogin() Response :", resp);
        //Message Success    navigate
        if (resp.data === "SUCCESS") {
          console.log(" .");
          navigate("/home");

          //  Fail  , FailMessgae
        } else if (resp.data === "FAIL") {
          console.log("  .");
          setLoginFailMessage(true);
        }
      })
      .catch((error) => {
        console.log("onLogin() ErrorMessage :", error);
      });
  };

  return (
    <div className="login_box">
      <div className="login_logo">
        <h1>OpenSales</h1>
      </div>
      <div className="login_form">
        <fieldset className="login_field">
          <form method="post" onKeyPress={onKeyPress}>
            <div className="account_txt"></div>
            <div className="account_input">
              <input type="text" placeholder="" onChange={onChangeAccount} value={userAccount}></input>
            </div>
            <div className="password_txt"></div>
            <div className="password_input">
              <input type="password" placeholder="" onChange={onChangePassword}></input>
            </div>
            {loginFailMessage === true && <div className="login_error_msg">   .</div>}
            <div className="find_account_div">
              {/* <span className="find_account" onClick={findAccount}>
                 
              </span> */}
            </div>
            <br />
            <div className="submit_btn">
              <input className="button_submit" type="button" value="" onClick={onLogin}></input>
            </div>
          </form>
        </fieldset>
        {/* <div className="sign_up">
          <div className="ment_signup">  ?</div>
          <div className="link_signup" onClick={signUp}>
            SignUp
          </div>
        </div> */}
      </div>
    </div>
  );
}

export default Login;
