import { useState, useEffect, useRef } from "react";
import { useNavigate } from "react-router-dom";

import axios from "axios";
function UpdateProfile() {
  // input   state .
  const [firstnm, setfirstnm] = useState("");
  const [lastnm, setlastnm] = useState("");
  const [email, setemail] = useState("");
  const [id, setid] = useState("");
  const [password, setpassword] = useState("");
  const [confirmpw, setconfirmpw] = useState("");
  const [prevPwCheck, setPrevPwCheck] = useState("");
  const [userInfo, setUserInfo] = useState([]);
  const fileInput = useRef(null);
  const [image, setImage] = useState("https://cdn.pixabay.com/photo/2015/10/05/22/37/blank-profile-picture-973460_1280.png");

  // User-account      
  useEffect(() => {
    let url = "/userinfo";

    const fetchData = async () => {
      const result = await axios.get(url);
      result.data.password = "";
      //user,  ,  ,    
      setUserInfo(result);
      setImage(result.data.userProfilePicPath);
      setfirstnm(result.data.userProfileId.firstName);
      setlastnm(result.data.userProfileId.lastName);
      setemail(result.data.userProfileId.email);
      setid(result.data.userProfileId.userAccount);
      //console.log(result);
      if (result.data.userProfileId.userAccount === "" || result.data.userProfileId.userAccount === undefined || result.length === 0) {
        // getUserInfo()
        navigate("/login");
      }
    };
    fetchData();
  }, []);

  //  Navigate
  const navigate = useNavigate("");

  //input State   

  // state  
  const onEmail = (e) => {
    setemail(e.currentTarget.value);
  };
  // state  
  const onPassword = (e) => {
    setpassword(e.currentTarget.value);
  };
  //  state  
  const onConfirmpw = (e) => {
    setconfirmpw(e.currentTarget.value);
  };
  //   state  
  const onPrevPWCheck = (e) => {
    setPrevPwCheck(e.currentTarget.value);
    checkPrevPasswordCheck();
  };

  //Submit  Validate check  (   )
  const validate = () => {
    let result = false;
    if (password !== confirmpw) {
      alert("  .");
      return result;
    } else {
      result = true;
      return result;
    }
  };
  const checkPrevPasswordCheck = (password) => {};
  // Insert(create) 
  const updateUserAccount = () => {
    //axios  api 
    // Validate  
    let validateKey = validate();

    if (validateKey) {
      axios({
        url: "/regist/account/update",
        headers: {
          "Content-Type": "application/json",
        },
        withCredentials: true,
        method: "POST",
        data: {
          profile_pic: image,
          user_account: userInfo.data.userProfileId.userAccount,
          email: email,
          password: password,
        },
      })
        //   ,  
        .then((res) => {
          console.log(res);
          navigate("/home");
        })
        .catch((err) => {
          console.log(err);
        });
    }
  };
  const onImgChange = async (e) => {
    if (e.target.files[0]) {
      setImage(e.target.files[0]);
    } else {
      //  
      setImage(
        //     
        "https://cdn.pixabay.com/photo/2015/10/05/22/37/blank-profile-picture-973460_1280.png"
      );
      return;
    }
    //   
    const reader = new FileReader();
    reader.onload = () => {
      if (reader.readyState === 2) {
        setImage(reader.result);
      }
    };
    reader.readAsDataURL(e.target.files[0]);
  };
  return (
    <div className="signup_parent_box">
      <div className="signup_box">
        <div className="signup_box_tw">
          <div className="signup_title"> </div>
          <div className="signup_input">
            <form>
              <div className="signup_profile_pic">
                <img
                  className="signup_img"
                  src={image}
                  style={{ width: "150px", height: "150px" }}
                  onClick={() => {
                    fileInput.current.click();
                  }}
                  alt=""
                />
                <input type="file" accept="image/*" style={{ display: "none" }} onChange={onImgChange} ref={fileInput} />
              </div>
              <div className="signup_name">
                <input type="text" placeholder="" value={lastnm} readOnly />
                <input type="text" placeholder="" value={firstnm} readOnly />
              </div>
              <div className="signup_email">
                <input type="email" placeholder="" value={email} onChange={onEmail} />
              </div>
              <div className="signup_id">
                <input type="text" placeholder="" value={id} readOnly />
              </div>
              {/* <div className="signup_password">
                <input
                  type="password"
                  placeholder="   "
                  onChange={onPrevPWCheck}
                />
              </div> */}
              <div className="signup_password">
                <input type="password" placeholder="" onChange={onPassword} />
              </div>
              <div className="signup_confirm_pw">
                <input type="password" placeholder=" " onChange={onConfirmpw} />
              </div>

              <div className="signup_btn">
                <input type="button" value="" onClick={updateUserAccount} />
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
}
export default UpdateProfile;
