import { useState, useRef } from "react";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import "./Login.css";
function SignUp() {
  // input   state .
  const [firstnm, setfirstnm] = useState(""); // 
  const [lastnm, setlastnm] = useState(""); // 
  const [email, setemail] = useState(""); // 
  const [id, setid] = useState(""); // 
  const [password, setpassword] = useState(""); //
  const [confirmpw, setconfirmpw] = useState(""); // 
  const fileInput = useRef(null);
  // const formData = new FormData();
  const [image, setImage] = useState("https://cdn.pixabay.com/photo/2015/10/05/22/37/blank-profile-picture-973460_1280.png");

  //  Navigate
  const navigate = useNavigate("/login");

  //input State   

  //   
  const onFirstName = (e) => {
    setfirstnm(e.currentTarget.value);
  };
  //   
  const onLastName = (e) => {
    setlastnm(e.currentTarget.value);
  };
  //   
  const onEmail = (e) => {
    setemail(e.currentTarget.value);
  };
  //   
  const onId = (e) => {
    setid(e.currentTarget.value);
  };
  //   
  const onPassword = (e) => {
    setpassword(e.currentTarget.value);
  };
  //   
  const onConfirmpw = (e) => {
    setconfirmpw(e.currentTarget.value);
  };

  //Submit  Validate check  (   )
  const validate = () => {
    const result = true;
    const regex = /^[^\s@]+@[^\s@]+\.[^\s@]{2,}$/i;
    if (!firstnm || firstnm === "") {
      return false;
    }
    if (!lastnm || lastnm === "") {
      return false;
    }
    if (!id || id === "") {
      return false;
    }
    if (!password || password === "") {
      console.log(" .");
      return false;
    }
    if (!confirmpw || confirmpw === "") {
      console.log(" .");
      return false;
    }

    if (password !== confirmpw) {
      result = false;
      alert("  .");
      return result;
    }

    if (!email || email === "") {
      setemail("Cannot be blank");
      //    
      return false;
    }
    return true;
  };
  //  
  const makeUserAccount = () => {
    //axios  api 
    // let validateKey = validate();
    // if (validateKey) {
    axios({
      url: "regist/account",
      headers: {
        "Content-Type": "application/json",
      },
      withCredentials: true,
      method: "POST",
      data: {
        usr_profile: image,
        user_name: lastnm + firstnm,
        user_account: id,
        first_nm: firstnm,
        last_nm: lastnm,
        email: email,
        password: password,
      },
    })
      //   ,  
      .then((res) => {
        console.log(res);
        if (res.data === "ACCOUNT" || res.data === "EMAIL") {
          alert(res.data + "()   " + res.data + ".");
        } else {
          navigate("/Home");
        }
      })
      .catch((err) => {
        console.log(err);
      });
    // }
  };
  const onImgChange = async (e) => {
    if (e.target.files[0]) {
      setImage(e.target.files[0]);
    } else {
      //  
      setImage(
        //     
        "https://cdn.pixabay.com/photo/2015/10/05/22/37/blank-profile-picture-973460_1280.png"
      );
      return;
    }
    //   
    const reader = new FileReader();
    reader.onload = () => {
      if (reader.readyState === 2) {
        setImage(reader.result);
      }
    };
    reader.readAsDataURL(e.target.files[0]);
  };
  return (
    <div className="signup_parent_box">
      <div className="signup_box">
        <div className="signup_box_tw">
          <div className="signup_title"> </div>
          <div className="signup_input">
            <form>
              <div className="signup_profile_pic">
                <img
                  className="signup_img"
                  src={image}
                  style={{ width: "150px", height: "150px" }}
                  onClick={() => {
                    fileInput.current.click();
                  }}
                  alt=""
                />
                <input type="file" accept="image/*" style={{ display: "none" }} onChange={onImgChange} ref={fileInput} />
              </div>
              {/**,  , ,  input */}
              <div className="signup_name">
                <input type="text" placeholder="" onChange={onLastName} />
                <input type="text" placeholder="" onChange={onFirstName} />
              </div>
              <div className="signup_email">
                <input type="email" placeholder="" onChange={onEmail} />
              </div>
              <div className="signup_id">
                <input type="text" placeholder="" onChange={onId} />
              </div>
              {/**  input */}
              <div className="signup_password">
                <input type="password" placeholder="" onChange={onPassword} />
              </div>
              {/**   input */}
              <div className="signup_confirm_pw">
                <input type="password" placeholder=" " onChange={onConfirmpw} />
              </div>
              {/**   */}
              <div className="signup_btn">
                <input type="button" value="" onClick={makeUserAccount} />
              </div>
            </form>
          </div>
        </div>
      </div>
      {/* <div className="already_account">  ?</div> */}
    </div>
  );
}
export default SignUp;
